const Notification = ({message}) => {

    if(!message) return null;

    const type = message[1];

    console.log('message:: ', message)

    if(message[0] === null){
        return null
    }
    
    return(
        <div>
            <p className={type}>{message[0]}</p>
        </div>
    )
} 

export default Notification